// src/features/auth/openid/openIdClient.js
import {
  OPENID_SESSION_CHANGE_EVENT,
  OPENID_SESSION_KEY,
} from "./constants";

function emitSessionChange(detail) {
  if (typeof window === "undefined") {
    return;
  }

  const event = new CustomEvent(OPENID_SESSION_CHANGE_EVENT, {
    detail,
  });

  window.dispatchEvent(event);
}

async function parseResponse(response) {
  const payload = await response.json();

  if (!response.ok || !payload?.success) {
    const error = payload?.error ?? "OpenID 인증에 실패했습니다.";
    throw new Error(error);
  }

  return payload;
}

export async function authenticateWithDummyProvider({
  providerId,
  returnUrl = "/",
}) {
  const response = await fetch("/api/auth/login", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ providerId, returnUrl }),
  });

  const payload = await parseResponse(response);

  if (typeof window !== "undefined") {
    sessionStorage.setItem(OPENID_SESSION_KEY, JSON.stringify(payload));
    emitSessionChange(payload);
  }

  return payload;
}

export function loadStoredSession() {
  if (typeof window === "undefined") {
    return null;
  }

  const raw = sessionStorage.getItem(OPENID_SESSION_KEY);
  if (!raw) {
    return null;
  }

  try {
    return JSON.parse(raw);
  } catch (error) {
    console.error("Failed to parse stored OpenID session", error);
    return null;
  }
}

export function clearStoredSession() {
  if (typeof window === "undefined") {
    return;
  }

  sessionStorage.removeItem(OPENID_SESSION_KEY);
  emitSessionChange(null);
}
