"use client";

// src/app/login/page.jsx
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

import { Button } from "@/components/ui/button";
import {
  DUMMY_OPENID_PROVIDER,
  OPENID_SESSION_KEY,
} from "@/features/auth/openid/constants";
import {
  authenticateWithDummyProvider,
  clearStoredSession,
  loadStoredSession,
} from "@/features/auth/openid/openIdClient";

export default function LoginPage() {
  const router = useRouter();
  const [session, setSession] = useState(() => loadStoredSession());
  const [status, setStatus] = useState("idle");
  const [message, setMessage] = useState("");

  useEffect(() => {
    if (typeof window === "undefined") {
      return undefined;
    }

    const handleStorage = (event) => {
      if (event.storageArea && event.storageArea !== sessionStorage) {
        return;
      }

      if (event.key && event.key !== OPENID_SESSION_KEY) {
        return;
      }

      setSession(loadStoredSession());
    };

    window.addEventListener("storage", handleStorage);
    return () => window.removeEventListener("storage", handleStorage);
  }, []);

  const handleLogin = async () => {
    setStatus("loading");
    setMessage("");

    try {
      const payload = await authenticateWithDummyProvider({
        providerId: DUMMY_OPENID_PROVIDER.id,
        returnUrl: "/",
      });

      setSession(payload);
      setStatus("success");
      setMessage(payload.message ?? "로그인에 성공했습니다.");

      setTimeout(() => {
        router.push(payload.redirectUrl ?? "/");
      }, 600);
    } catch (error) {
      setStatus("error");
      setMessage(error.message ?? "로그인 중 오류가 발생했습니다.");
    }
  };

  const handleLogout = () => {
    clearStoredSession();
    setSession(null);
    setStatus("idle");
    setMessage("세션이 초기화되었습니다.");
  };

  return (
    <div className="flex min-h-[600px] flex-col items-center justify-center gap-10 py-12">
      <div className="w-full max-w-lg rounded-xl border bg-background p-10 shadow-sm">
        <div className="space-y-2 text-center">
          <h1 className="text-3xl font-semibold">OpenID SSO 로그인</h1>
          <p className="text-muted-foreground">
            {DUMMY_OPENID_PROVIDER.description}
          </p>
        </div>

        <div className="mt-8 space-y-6">
          <div className="rounded-lg border border-dashed bg-muted/30 p-6 text-left">
            <h2 className="text-lg font-medium text-foreground">
              {DUMMY_OPENID_PROVIDER.name}
            </h2>
            <p className="mt-2 text-sm text-muted-foreground">
              실제 IdP 연동 시 OpenID Authorization Code Flow를 대체할 수 있도록
              구성된 더미 로그인 절차입니다.
            </p>
            <Button
              className="mt-6 w-full"
              size="lg"
              disabled={status === "loading"}
              onClick={handleLogin}
            >
              {status === "loading" ? "로그인 처리 중..." : "SSO로 로그인"}
            </Button>
          </div>

          {session ? (
            <div className="space-y-2 rounded-lg border bg-accent/30 p-5 text-sm">
              <p>
                <span className="font-semibold">세션 사용자:</span> {session.user?.name}
              </p>
              <p>
                <span className="font-semibold">이메일:</span> {session.user?.email}
              </p>
              <p>
                <span className="font-semibold">토큰 만료:</span> {session.tokens?.expiresAt}
              </p>
              <div className="flex gap-2 pt-2">
                <Button
                  variant="outline"
                  className="flex-1"
                  onClick={() => router.push(session.redirectUrl ?? "/")}
                >
                  대시보드로 이동
                </Button>
                <Button variant="ghost" className="flex-1" onClick={handleLogout}>
                  세션 초기화
                </Button>
              </div>
            </div>
          ) : null}

          {message ? (
            <p
              className={`text-sm ${
                status === "error"
                  ? "text-destructive"
                  : "text-muted-foreground"
              }`}
            >
              {message}
            </p>
          ) : null}
        </div>
      </div>
    </div>
  );
}
